
Template.__checkName("notifications");
Template["notifications"] = new Template("Template.notifications", (function() {
  var view = this;
  return [ HTML.A({
    href: "#",
    class: "dropdown-toggle",
    "data-toggle": "dropdown",
    role: "button",
    "aria-haspopup": "true",
    "aria-expanded": "false"
  }, "\n    ", HTML.Raw("<!--Notificaciones-->"), "\n    ", HTML.Raw('<span class="glyphicon glyphicon-inbox"></span>'), "\n\n      ", Blaze.If(function() {
    return Spacebars.call(view.lookup("notificationCount"));
  }, function() {
    return [ "\n        ", Blaze.If(function() {
      return Spacebars.call(view.lookup("modeDisplay"));
    }, function() {
      return [ "\n          ", HTML.SPAN({
        class: "badge badge-inverse"
      }, Blaze.View("lookup:notificationCount", function() {
        return Spacebars.mustache(view.lookup("notificationCount"));
      })), "\n        " ];
    }, function() {
      return [ "\n          ", Blaze.If(function() {
        return Spacebars.call(view.lookup("infoStateCollapseNavBar"));
      }, function() {
        return [ "\n            ", HTML.SPAN({
          class: "badge badge-inverse"
        }, Blaze.View("lookup:notificationCount", function() {
          return Spacebars.mustache(view.lookup("notificationCount"));
        })), "\n          " ];
      }), "\n        " ];
    }), "\n      " ];
  }), "\n  "), "\n  ", HTML.UL({
    class: "notification dropdown-menu dropdown-menu-center"
  }, "\n    ", Blaze.If(function() {
    return Spacebars.call(view.lookup("notificationCount"));
  }, function() {
    return [ "\n      ", Blaze.Each(function() {
      return Spacebars.call(view.lookup("notifications"));
    }, function() {
      return [ "\n        ", Spacebars.include(view.lookupTemplate("notificationItem")), "\n      " ];
    }), "\n    " ];
  }, function() {
    return [ "\n      ", HTML.LI(HTML.SPAN("No Notifications")), "\n    " ];
  }), "\n  ") ];
}));

Template.__checkName("notificationItem");
Template["notificationItem"] = new Template("Template.notificationItem", (function() {
  var view = this;
  return HTML.LI("\n    ", HTML.A({
    href: function() {
      return Spacebars.mustache(view.lookup("pathFor"), "twiitPage");
    }
  }, "\n      ", HTML.STRONG(Blaze.View("lookup:twiitNotifUserName", function() {
    return Spacebars.mustache(view.lookup("twiitNotifUserName"));
  })), " ha twiiteado\n    "), "\n  ");
}));
