
Template.__checkName("tweetFeed");
Template["tweetFeed"] = new Template("Template.tweetFeed", (function() {
  var view = this;
  return HTML.DIV({
    class: "tweetfeed-container"
  }, "\n    ", HTML.DIV({
    class: "panel panel-default tweetfeed"
  }, "\n      ", HTML.DIV({
    class: "panel-body"
  }, "\n        ", HTML.Raw("<!-- Texto para el contenido del Twitt -->"), "\n        ", Blaze.Each(function() {
    return Spacebars.call(view.lookup("tweetMessage"));
  }, function() {
    return [ "\n          ", HTML.DIV({
      class: "panel panel-info"
    }, HTML.Comment("Contenido de la cabecera + el mensaje del twiit"), "\n            ", HTML.DIV({
      class: "panel-heading"
    }, HTML.Comment("Cabecera del twiit"), "\n              ", HTML.H3({
      class: "panel-title"
    }, "@", Blaze.View("lookup:..user", function() {
      return Spacebars.mustache(Spacebars.dot(view.lookup("."), "user"));
    }), " \n                ", HTML.SPAN({
      class: "glyphicon glyphicon-triangle-right",
      "aria-hidden": "true"
    }), "\n                ", Blaze.View("lookup:convertDateTime", function() {
      return Spacebars.mustache(view.lookup("convertDateTime"));
    }), "\n              "), "\n            "), HTML.Comment("Cabecera del twiit"), "\n            ", HTML.DIV({
      class: "panel-body"
    }, HTML.Comment("Contenido del twiit"), "\n              ", Blaze.View("lookup:..message", function() {
      return Spacebars.mustache(Spacebars.dot(view.lookup("."), "message"));
    }), "\n            "), HTML.Comment("Contenido del twiit"), "\n\n            ", HTML.DIV({
      class: "btn-group",
      role: "group",
      id: "btnGroupInteractions"
    }, "\n              ", HTML.BUTTON({
      type: "button",
      class: "btn btn-secondary"
    }, HTML.SPAN({
      class: "glyphicon glyphicon-bullhorn"
    })), "\n              ", HTML.BUTTON({
      type: "button",
      class: "btn btn-secondary"
    }, HTML.SPAN({
      class: "glyphicon glyphicon-comment"
    })), "\n              ", HTML.BUTTON({
      type: "button",
      id: "btnFav",
      class: "btn btn-secondary"
    }, HTML.SPAN({
      class: "glyphicon glyphicon-heart"
    }), HTML.SPAN({
      class: "badge-numFav badge"
    }, Blaze.View("lookup:..numFav", function() {
      return Spacebars.mustache(Spacebars.dot(view.lookup("."), "numFav"));
    }))), "\n            "), "\n          "), HTML.Comment("Contenido de la cabecera + el mensaje del twiit"), "\n        " ];
  }), "\n      "), "\n    "), "\n  ");
}));
